<template>
      <div>
        <mt-header title="客户管理" id="header">
          <router-link to="/" slot="left" >
            <mt-button icon="back"></mt-button>
          </router-link>
        </mt-header>
        <div class="clientmsg" >
          <div class="client" v-for="val in arr">
            <p class="cname">客户姓名：<span v-text="val.name" >13473549413</span></p>
            <p>身份证号：<span>******************</span></p>
            <p>添加时间：<span v-text="val.createTime">2018.3.6 21:03</span></p>
            <p>客户状态：<span>正式客户</span></p>
            <div>金牌会员</div>
          </div>
        </div>
      </div>

</template>

<script>
  import axios from 'axios'
    export default {
        name: "clientmsg",
      data(){
          return{
            arr:[ ]
          }
      },
      methods:{
        getdata(){
          let that = this;
          axios.get('/api/userlist/get',{withCredentials:true})
            .then(function (response) {
              that.arr = response.data.data
              console.log(that.arr);
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      },
      mounted(){
          console.log(123)
        this.getdata()
      }

    }
</script>

<style scoped>
  .cname{
    font-size: 22px;
    font-weight: 700;
  }
  .clientmsg{
    width: 330px;
    margin: 50px auto 50px;

  }
  .clientmsg .client{
    width:330px ;
    background: #fff;
    margin: 20px auto 0;
  }
 #header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
  }
</style>



